import topSearch from "@/components/topSearch.vue";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _pageSize = 20
let _this,timer
export default {
	components:{
		topSearch,
		uniLoadMore
	},
	data() {
		return {
			loadingType: 'more',
			customerList:[],
			keyword:'',
			pageNum: 1,
			isLastPage:false
		};
	},
	onReachBottom: function () {
		if (timer != null) {
		    clearTimeout(timer);
		       }
		  timer = setTimeout(function() {
		       _this.getMoreCustomer('',_this.pageNum,_pageSize);
		   }, 1000);

	 },
	 onPullDownRefresh: function () {
		_this.getCustomerList('',_this.pageNum,_pageSize);
	 },
	onLoad:function(){
		_this = this
		this.getCustomList()
		console.log(this.$store.state.inquiryNumber)
	},
	methods:{
		getMoreCustomer:function(keyword,pageNum,pageSize){
			    if (_this.loadingType !== 'more') {//loadingType!='more';直接返回
			    	return false;
			    }
				_this.loadingType = 'loading';
				uni.showNavigationBarLoading();//显示加载动画
				let url =  this.Api.companyList
			    let data= {
					keyword: '',  //	搜索关键字
					pageNum: this.pageNum,		   //当前页数
					pageSize: _pageSize
				}
			   this.myRequest(data,url,'get').then(res => {
				   if (res.data.data.list.length == 0) {//没有数据
				        console.log("no data")
				    	_this.loadingType = '';
				    	uni.hideNavigationBarLoading();//关闭加载动画
				    	return;
				   }
				 _this.pageNum++;//每触底一次 page +1
				  console.log(res)
				  _this.customerList = _this.customerList.concat(res.data.data.list)
				 _this.loadingType = 'more';//将loadingType归0重置
				 uni.hideNavigationBarLoading();//关闭加载动画
				 
			    }).catch(err => {
			     wx.showToast({
			       title: err.data.errMsg,
			       icon: 'none'
			     });
			   });
		},
		handleSearch:function(e){
			console.log(e)
			this.keyword = e
			let url =  this.Api.companyList
			let data ={
				keyword: this.keyword,  //	搜索关键字
				pageNum: 1,		   //当前页数
				pageSize: _pageSize
			}
			this.myRequest(data,url,'get').then(res => {
			  console.log(res);
				 _this.customerList = res.data.data.list
			 }).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
			
		},
		getCustomList:function(){
			_this.pageNum = 1
			_this.loadingType = 'more';
			uni.showNavigationBarLoading();
			
			let url =  this.Api.companyList
			let data ={
				keyword: '',  //	搜索关键字
				pageNum: this.pageNum,		   //当前页数
				pageSize: _pageSize
			}
			this.myRequest(data,url,'get').then(res => {
				_this.pageNum++;
			  console.log(res);
				_this.customerList = res.data.data.list
			 }).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();//得到数据后停止下拉刷新
		},
		toClientDetail:function(buyOrSellCode,companyCode){
			// console.log(realName,code)
			// var pages = getCurrentPages();
			// 	var currPage = pages[pages.length - 1]; //当前页面
			// 	var prevPage = pages[pages.length - 2]; //上一个页面
			// 	//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
			// 	// prevPage.setData({
			// 	// 		buyer:  realName,
			// 	// 		buyerCode: code
			// 	// })
			// 	prevPage.$vm.seller = realName
			// 	prevPage.$vm.sellerCode = code
			// uni.navigateBack({
			// 	delta: 1
			// });
			uni.navigateTo({
				url: './selectProduct?buyOrSellCode='+buyOrSellCode+'&companyCode='+companyCode,
				success: res => {},
				fail: (err) => {console.log(err)},
				complete: () => {}
			});
		}
	}
}
